import React, { Component } from 'react';
import Form from './Form';
import GrainList from './GrainList';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
//import './App.css';

class App extends Component {
  render() {
    return (
      <section className="section">
        <div className="container is-fluid">
          <h1 className="title">
            Tracker Calculator
          </h1>
        </div>
        <Router>
          <div className="container">
            <nav className="navbar" role="navigation" aria-label="main navigation">
              <div className="navbar-menu">
                <div className="navbar-start">
                  <div className="navbar-item">
                  <Link to="/regular-grain-position" className="navbar-item">Regular Grain Position</Link>
                  </div>
                  <div className="navbar-item">
                  <Link to="/grain-list-composer" className="navbar-item">Grain list Composer</Link>
                  </div>
                </div>
              </div>
            </nav>
            {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
            <Switch>
              <Route path="/regular-grain-position">
                <Form />
              </Route>
              <Route path="/grain-list-composer">
                <GrainList />
              </Route>
            </Switch>
          </div>
        </Router>
      </section>
    );
  }
}





export default App;



